
import {pageList} from '@/api/user.js'
export default {
    data() {
        return {
            requestFunc:pageList,
            searchParams: {
                postTitle: '',
                postType: '',
                postStatus: '',
            },
            tableData:[]
        }
    },
    methods: {
        returnData(pageList){
            this.tableData = pageList.list
        }
    },
}
