<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="categoriesList">
        <ToolBar>
            <el-button type="primary" icon="el-icon-plus" size="small">新增模块</el-button>
            <div>
                <el-select v-model="searchParams.postType" size="small" clearable placeholder="导航名称"
                           style="width: 140px">
                    <el-option
                            v-for="(v,k) in $Cfg.postType"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>

                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="模块分类"
                           style="width: 120px">
                    <el-option
                            v-for="(v,k) in $Cfg.postStatus"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>
                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="显示状态"
                           style="width: 120px">
                    <el-option
                            v-for="(v,k) in $Cfg.postStatus"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>
                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="操作人"
                           style="width: 120px">
                    <el-option
                            v-for="(v,k) in $Cfg.postStatus"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>
                <el-button type="success" size="small" @click="$refs.pagination.Refresh()">查询</el-button>
                <el-button type="warning" size="small" @click="clearSearchParams()">重置</el-button>
            </div>
        </ToolBar>
        <el-table
                :data="tableData"
                border
                style="width: 100%">
            <el-table-column
                    prop="id"
                    label="ID"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="导航名称">
            </el-table-column>
            <el-table-column
                    prop="address"
                    width="180"
                    label="模块分类">
            </el-table-column>
            <el-table-column
                    prop="address"
                    label="显示状态">
            </el-table-column>
            <el-table-column
                    prop="address"
                    label="最近操作人">
            </el-table-column>
            <el-table-column
                    prop="address"
                    label="创建时间">
            </el-table-column>
            <el-table-column
                    label="操作"
                    width="306">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)"  size="small"
                               >禁用</el-button>
                    <el-button @click="$router.push({path:'/channel/categories_list/category_course_details'})" type="success" size="small"
                    >课程详情</el-button>
                    <el-button @click="editCategory(true,scope.row)" type="primary" size="small"
                               >修改</el-button>
                    <el-button @click="handleClick(scope.row)" type="danger" size="small"
                               >删除</el-button>

                </template>
            </el-table-column>
        </el-table>
        <Pagination
            ref="pagination"
            :params="searchParams"
            :requestFunc="requestFunc"
            @returnData="returnData"
        />
    </div>
</template>

<script>
    import ToolBar from '@/components/ToolBar.vue';
    import HelpHint from '@/components/HelpHint.vue';
    import Pagination from '@/components/Pagination.vue';
    import paginationMixin from './mixin/page.js'
    import Edit from './Edit.vue'
    export default {
        mixins:[paginationMixin],
        data() {
            return {
                ruleForm:{name:'sdfsd',region:''}
            }
        },

        methods: {
            editCategory(isEdit,row){
                const h = this.$createElement;
                this.$msgbox({
                    title: isEdit ? '修改模块分类':'新增模块分类',
                    message: h('Edit', {props:{ruleForm:this.ruleForm}},),
                    showCancelButton: true,
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    beforeClose: (action, instance, done) => {
                        if (action === 'confirm') {
                            console.log(instance.name)
                            instance.confirmButtonLoading = true;
                            instance.confirmButtonText = '执行中...';
                            setTimeout(() => {
                                done();
                                setTimeout(() => {
                                    instance.confirmButtonLoading = false;
                                }, 300);
                            }, 1000);
                        } else {
                            done();
                        }
                    }
                }).then(action => {
                    this.$message({
                        type: 'info',
                        message: isEdit ? '修改模块分类成功' :'新增模块分类成功'
                    });
                }).catch(_=>{})
            },
            handleClick(row) {
                this.$alert(row, '标题名称', {
                    confirmButtonText: '确定',
                    callback: action => {}
                });
            },
        },
        created() {

        },
        components: {
            ToolBar, HelpHint, Pagination ,Edit
        }
    }
</script>
